import React, { useEffect, useState } from 'react';
import Chatbox from "./components/Chatbox";
import Chats from "./components/Chats"
import Nav from "./components/Nav";
// import Errorpage from "./components/Errorpage";
import './App.css';
import firebase, {firestore, db, auth} from "../src/config" 

import Modal from "react-modal";

Modal.setAppElement("#root");


function App() {
  const [chatInput, setChatInput] = useState({
    "chatText": "",
    "name":"",
    "time":0,
    "dateTime":"",
  });
  const [chatsList, setChatsList] = useState([]);

  const [user, setUser] = useState({
    "userName":"",
    "email": "",
    "password": "",
    "signedIn":false,
  });



  function handleChatInput(e) {
    const {name, value} = e.target;
    setChatInput((prev) => {
      return {...prev,
        [name] : value}
    });
  }

  function addToDB(mssgObj) {
    // console.log(mssgObj);
    db.collection("newChats").add(mssgObj)
    // .then((docRef)=>{console.log(docRef.id);})
    // .catch((error)=>{console.log("Erroe occured "+error);})
  }

  

  function handleSubmit(e) {
    e.preventDefault();
    if (chatInput.chatText === "")return
    let date = new Date();
    chatInput.time = Date.now();
    let hrs = date.getHours();
    let min = date.getMinutes();
    chatInput.dateTime = (hrs%12)+":"+(min)+(hrs >= 12 ? "pm":"am");
    setChatsList(prev=>{return([...prev, chatInput]);});
    addToDB(chatInput);
    setChatInput(prev=>{return {...prev, chatText:""}});
    
  }

  function fetchChats() {
    const chatsRef = db.collection("newChats").orderBy("time");
    chatsRef.onSnapshot((ss)=>{
      let arr = [];
      ss.forEach((doc)=>{
        arr.push(doc.data())
      })
      setChatsList(arr);
      scrollToBottom()
    });
  }

  function signUP(user) {
    firebase.auth().createUserWithEmailAndPassword(user.email, user.password)
    .then((userCredential) => {
      // console.log(user, "signed UP");
      firebase.auth().currentUser.updateProfile({
                      displayName: user.userName,
                  });
      setUser((prev)=>{return{...prev, signedIn:true}})// userName: currUser.displayName}});
      // setAuthError({});
    
    })
    .catch((error) => {
      // var errorCode = error.code;
      var errorMessage = error.message;
      // setAuthError(error);
      alert(errorMessage);
      // console.log(errorCode, errorMessage);
    });
  }

  function signIn(user) {
    firebase.auth().signInWithEmailAndPassword(user.email, user.password)
    .then((userCredential) => {
      setUser((prev)=>{return{...prev, signedIn:true}})// userName: currUser.displayName}})
      // console.log(user, "signedIN");
      // setAuthError({});
    })
    .catch((error) => {
      // var errorCode = error.code;
      var errorMessage = error.message;
      // setAuthError(error);
      alert(errorMessage);
      // console.log(errorCode, errorMessage);
    });
  }

  function logOut() {
    auth.signOut();
    setUser({"userName":"","email": "","password": ""})
  }

  function handleUserChange(e) {
    const {name, value} = e.target;
    setUser((prev)=>{
      return ({
        ...prev,
        [name] : value,
      });
    });
  }

  const actions = {
    "signUp": signUP,
    "signIn":signIn,
    "logOut": logOut,
    "handleUserChange":handleUserChange,
  };

  function scrollToBottom() {
    const lis = document.querySelector(".chatList");
    lis.scrollIntoView(false);
    lis.style.transform = "translateY(-40px)";
  }

  useEffect(() => {
    // console.log(auth.currentUser);
    auth.onAuthStateChanged(currUser=>{
      // console.log(user,"is the user 1 ");
      if(currUser){
        setUser({"userName":currUser.displayName,"email": "","password": "", signedIn:true});
        setChatInput((prev)=>{return{...prev, name:currUser.displayName}})
        fetchChats();
        scrollToBottom();
      }
      // else console.log("pls sign in");
      })
  },[]);


  
  

  return (
    <div className="App">
    <Nav user={user} actions={actions} />
    {
      (user.signedIn) &&
    <>
    <Chats chatList={chatsList} user={user}/>
    <Chatbox onchange={handleChatInput} onclick={handleSubmit} text={chatInput.chatText}/>
    </> 
    }
    </div>
  );
}

export default App;
